import React from "react";
import style from "./recipe.module.css";

const openRecipe = url => {
  let win = window.open(url, "_blank");
  win.focus();
};

export default function Recipe({ title, calories, img, ingredients, link }) {
  const headerStyle = {
    backgroundImage: `url(${img})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
    borderTopLeftRadius: "10px",
    borderTopRightRadius: "10px",
    fontSize: "20px",
    cursor: "pointer"
  };
  return (
    <div className={style.recipe}>
      {/* <div className={style.header} onClick={openRecipe.bind(this, link)}> */}
      <div
        style={headerStyle}
        // className={style.header}
        onClick={openRecipe.bind(this, link)}
      >
        <h1
          style={{
            color: "rgb(255, 244, 224)",
            textShadow: "10px 5px 20px black"
          }}
          className={style.title}
        >
          {title}
        </h1>
        <img className={style.image} src={img} alt="" />
      </div>

      <div className={style.content}>
        <ol>
          {ingredients.map((ing, idx) => (
            <li key={idx}>{ing.text}</li>
          ))}
        </ol>
        {/* <p>{calories}</p> */}
      </div>

      {/* <img className={style.image} src={img} alt="" /> */}
    </div>
  );
}
