import React, { useEffect, useState } from "react";
import Recipe from "./Recipe";
import "./App.css";
import edamam from "./EDAMAM";
const testdata = require("./testdata.json");

const App = () => {
  const [recipes, setRecipes] = useState([]);
  const [search, setSearch] = useState("");
  const [query, setQuery] = useState("");

  useEffect(() => {
    getRecipes();
    // getRecipestest();
  }, [query]);

  const getRecipes = async () => {
    const { ID, KEY, URL } = edamam;
    const edamamSearch = `${URL}${query}&app_id=${ID}&app_key=${KEY}`;
    const response = await fetch(edamamSearch);
    const data = await response.json();
    setRecipes(data.hits);
    console.log(edamamSearch);
    console.log(data.hits);
  };

  const getRecipestest = async () => {
    setSearch("using testdata.json");
    setRecipes(testdata.hits);
  };

  const updateSearch = e => {
    setSearch(e.target.value);
  };

  const getSearch = e => {
    e.preventDefault();
    if (search.split(" ").join("")) {
      setQuery(search);
      setSearch("");
    } else {
      alert("No search value");
    }
  };

  return (
    <div className="App">
      <form onSubmit={getSearch} className="search-form">
        <input
          className="search-bar"
          type="text"
          value={search}
          onChange={updateSearch}
          placeholder="Search Recipes.."
        />
        <button className="search-btn" type="submit">
          Search
        </button>
      </form>
      <div className="recipe-cont">
        {recipes.map((recipe, idx) => (
          <Recipe
            title={recipe.recipe.label}
            calories={parseInt(recipe.recipe.calories)}
            img={recipe.recipe.image}
            key={idx}
            ingredients={recipe.recipe.ingredients}
            link={recipe.recipe.url}
          />
        ))}
      </div>
    </div>
  );
};

export default App;
